.advantages__header {
  font-family: "RobotoCondensed", "Arial", sans-serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 24px;
  margin: 0;
  margin-bottom: 21px;
  color: $baby-blue;
}

.advantages__text {
  line-height: 22px;
}

@media (max-width: $tablet-width) {
  .advantages {
    padding-top: 54px;
    padding-right: 25px;
    padding-bottom: 10px;
    padding-left: 25px;
  }

  .advantages__item {
    width: 47.9%;
    min-height: 233px;
    padding-top: 73px;
    padding-right: 20px;
    padding-bottom: 3px;
    padding-left: 25px;
    border-left: 2px solid $baby-blue;

    &--time::before {
      top: 24px;
      left: 26px;
      width: 29.38px;
      height: 35.46px;
      background-size: contain;
    }

    &--suppliers::before {
      top: 23px;
      left: 25px;
      width: 31.72px;
      height: 35.18px;
      background-size: contain;
    }

    &--confidentiality::before {
      top: 27px;
      left: 26px;
      width: 30.31px;
      height: 26.1px;
      background-size: contain;
    }

    &--price::before {
      top: 25px;
      left: 26px;
      width: 32.18px;
      height: 32.18px;
      background-size: contain;
    }
  }

  .advantages__header {
    line-height: 20px;
    margin-bottom: 16px;
  }
}

@media (max-width: $mobile-width) {
  .advantages {
    padding-top: 37px;
    padding-right: 16px;
    padding-left: 14px;
  }

  .advantages__list {
    flex-direction: column;
  }

  .advantages__item {
    width: 100%;
    margin-bottom: 20px;
    padding-top: 69px;

    &--suppliers {
      padding-right: 10px;
    }

    &--confidentiality::before {
      top: 26px;
    }
  }

  .advantages__header {
    font-size: 20px;
    line-height: 32px;
    margin-bottom: 5px;
  }

  .advantages__text {
    margin-top: 8px;
    margin-bottom: 20px;
  }
}
